@font-face {
  font-family: "icons-font";
  src: url('../icons/dist/fonts/icons-font.eot');
  src: url('../icons/dist/fonts/icons-font.eot?#iefix') format('eot'),
  url('../icons/dist/fonts/icons-font.woff') format('woff'),
  url('../icons/dist/fonts/icons-font.ttf') format('truetype'),
  url('../icons/dist/fonts/icons-font.svg#icons-font') format('svg');
}

@mixin icon-styles {
  font-family: "icons-font";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  // speak: none; // only necessary if not using the private unicode range (firstGlyph option)
  text-decoration: none;
  text-transform: none;
  vertical-align: top;
  line-height: 0.9em;
}

.icon{
  background-repeat: no-repeat;
  width: 1em;
  height: 1em;
  display: inline-block;
  background-size: contain;
  vertical-align: bottom;
  &%icon {
    @include icon-styles;
  }
}

